.loginSection {
    width: 1245px;
    height: 700px;
    margin: 0 auto;
    position: relative;
    margin-bottom: 90px;
}

.loginSection .loginImg {
    width: 615px;
    height: 700px;
    float: left;
    margin-right: 15px;
}

.loginSection .loginArea {
    width: 615px;
    height: 700px;
    float: left;
    box-sizing: border-box;
    padding-top: 42px;

}

.loginSection .loginArea .loginImg_logo {
    width: 174px;
    height: 47px;
    margin-left: 50%;
    transform: translateX(-50%);
    margin-bottom: 30px;
}

.loginSection .loginArea h1 {
    font-size: 24px;
    text-align: center;
    font-weight: normal;
    margin-bottom: 30px;
}

.loginArea form {
    width: 405px;
    margin: 0 auto;
}

.areaTitle {
    margin-bottom: 3px;
}

.areaTitle>span {
    color: red;
}

.inputArea {
    margin-bottom: 30px;
    position: relative;
}

.inputArea .inputDesign {
    width: 405px;
    height: 45px;
    border: 1px solid #555;
    border-radius: 5px;
    font-size: 16px;
    box-sizing: border-box;
    padding-left: 10px;
}

.inputArea .inputDesign:focus {
    outline: none;
    border: 2px solid #4136BF;
    box-sizing: border-box;
    background-color: white;

}

.inputArea .inputDesign:focus::placeholder {
    color: transparent;

}
.alert_capslock {
    position: absolute;
    right: 7px;
    top: 37px;
    color: red;
    font-size: 12px;
    font-weight: bold;
    user-select: none;
    transition: 0.3s;
    visibility: hidden;
    opacity: 0;
}

.pwArea .pw_nameplate {
    display: flex;
    align-items: center;
}

.pwArea .pw_nameplate p {
    margin-right: 15px;
}

.pwArea .pw_nameplate label {
    font-size: 12px;
    display: flex;
}

.pwArea .pw_nameplate label input#pw_show_id {
    margin-right: 5px;
    display: none;
}

#pw_show_id:checked~.eye_open {
    display: none;
}

#pw_show_id:checked~.eye_closed {
    display: block;
}

.pwArea .pw_nameplate label img {
    width: 15px;
    height: 15px;
}


.pwArea .pw_nameplate label .eye_open {
    display: block;
}

.pwArea .pw_nameplate label .eye_closed {
    display: none;
}


.pwArea .id_memArea {
    display: flex;
    justify-content: space-between;
    margin-top: 5px;
}

.id_memArea label {
    font-size: 12px;
}

.id_memArea label input {
    margin-right: 3px;
}

.id_memArea a {
    font-size: 12px;
}

.btnLogin {
    width: 405px;
    height: 45px;
    background-color: #FFBF00;
    text-align: center;
    border: none;
    border-radius: 5px;
    font-size: 14px;
    color: #333;
    cursor: pointer;
    margin-bottom: 15px;
}

.signupArea {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 30px;
}

.signupArea p {
    display: block;
    width: 100%;
    font-size: 14px;
}

.signupArea a {
    margin-top: 2px;
    display: block;
    width: calc((100% / 2) - 15px);
    height: 45px;
    text-align: center;
    line-height: 45px;
    background-color: white;
    border: 1px solid #555;
    border-radius: 5px;
}

.text_otherLogin {
    text-align: center;
    font-size: 14px;
    margin-bottom: 15px;
    position: relative;
}

.text_otherLogin::before {
    content: "";
    display: block;
    position: absolute;
    width: 130px;
    height: 1px;
    background-color: #555;
    left: 0;
    top: 10px;
}

.text_otherLogin::after {
    content: "";
    display: block;
    position: absolute;
    width: 130px;
    height: 1px;
    background-color: #555;
    right: 0;
    top: 10px;
}


.otherLoginArea {
    width: 405px;
    height: 45px;
    display: flex;
    justify-content: space-between;
    margin-bottom: 15px;
}

.otherLoginArea a {
    width: 125px;
    height: 45px;
    display: flex;
    align-items: center;
}

.otherLoginArea a:first-child {
    background-color: #00c73c;
    color: white;
}

.otherLoginArea a:nth-child(2) {
    background-color: #fddc3f;
    color: black;
}

.otherLoginArea a:last-child {
    background-color: white;
    color: black;
    border: 1px solid #ccc;
    box-sizing: border-box;
}

.otherLoginArea a img {
    width: 30px;
    height: 30px;
    margin-left: 10px;
    margin-right: 15px;
}

.otherLoginArea a p {
    font-size: 16px;
    font-weight: 400;
}

.login_terms {
    font-size: 12px;
}

.login_terms span.terms {
    text-decoration: underline;
    cursor: pointer;
}

.termsArea {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 50px;
    width: 500px;
    height: 620px;
    background-color: white;
    border: 1px solid #ccc;
    box-sizing: border-box;
    z-index: 50;
    transition: 0.3s;
    visibility: hidden;
    opacity: 0;
}

.termsArea .tremsIcon {
    display: flex;
    justify-content: space-between;
    margin-bottom: 15px;
}

.termsClose {
    cursor: pointer;
}

.termsArea .terms_detail {
    width: calc(100% - 35px);
    height: calc(100% - 50px);
    margin: 0 auto;
    overflow-y: scroll;
}

.login_alert {
    display: none;
    font-size: 14px;
    color: red;
    margin-bottom: 15px;
}
